<template>
	<view class="container">
		<!-- #ifdef MP-WEIXIN -->
		<!-- <tuicalling
		ref="TUICalling"
		id="TUICalling-component"
		:config="config">
	</tuicalling> -->
		<!-- #endif -->
		<view class="tui-chatroom-navigatorbar">
			<image class="tui-chatroom-navigatorbar-back" src="/static/back-left.png" @tap="goBack" />
			<!-- 先查 remark；无 remark 查 (c2c)nick/(group)name；最后查 (c2c)userID/(group)groupID -->
			<view class="conversation-title">{{conversationName}}</view>
		</view>
		<!-- 订单展示 -->
		<view class="msgorder" @click="checkOrder()" v-if="orderID>0">
			<image src="../../static/message/order.png" mode="aspectFit" class="order-img"></image>
			<view class="orderID">{{i18n.message.order}}:{{orderNumber}}</view>
			<image src="../../static/message/msg_back@2x.png" mode="aspectFit" class="orderback"></image>
		</view>
		<view class="group-profile"><TUI-group-profile v-if="isShow" id="groip-profile" :conversation="conversation" /></view>
		<view class="message-list" @tap="triggerClose"><TUI-message-list id="message-list" ref="messageList" :conversation="conversation" /></view>
		<view v-if="videoPlay" class="container-box" @tap.stop="stopVideoHander">
			<video
				v-if="videoPlay"
				class="video-message"
				:src="videoMessage.payload.videoUrl"
				:poster="videoMessage.payload.thumbUrl"
				object-fit="cover"
				error="videoError"
				autoplay="true"
				direction="0"
			/>
		</view>
		<view v-if="showChat" class="message-input">
			<TUI-message-input id="message-input" ref="messageInput" :conversation="conversation" @sendMessage="sendMessage" />
		</view>
	</view>
</template>

<script>
import logger from '../../utils/logger';
import TUIMessageList from '../../components/tui-chat/message-list/index';
import TUIMessageInput from '../../components/tui-chat/message-input/index';

const app = getApp();
export default {
	components: {
		TUIMessageList,
		TUIMessageInput,
	},
	computed: {
		i18n() {
			return this.$t('index')
		}
	},
	props: {},
	data() {
		return {
			conversationName: '',
			conversation: {},
			messageList: [],
			isShow: false,
			showChat: true,
			conversationID: '',
			videoPlay: false,
			videoMessage: {},
			config: {
				sdkAppID: '',
				userID: '',
				userSig: '',
				type: 1,
				tim: null
			},
			orderID:'',
			orderNumber:''
		};
	},
	created() {
		uni.$on('videoPlayerHandler', value => {
			this.videoPlay = value.isPlay;
			this.videoMessage = value.message;
		});
	},

	/**
	 * 生命周期函数--监听页面加载
	 */
	onLoad(options) {
		// conversationID: C2C、 GROUP
		logger.log(` TUI-chat | onLoad | conversationID: ${options.conversationID}`);
		const { conversationID } = options;
		this.setData({
			conversationID
		});
		// #ifdef MP-WEIXIN
		// this.config = {
		// 	sdkAppID: app.globalData.SDKAppID,
		// 	userID: app.globalData.userInfo.userID,
		// 	userSig: app.globalData.userInfo.userSig
		// }
		// logger.log(`TUI-chat | TUICalling-config  | config:${JSON.stringify(this.callingConfig)}`);
		// // 挂载在 uni 上
		// uni.$wxTUICalling = this.$refs.TUICalling;
		// 		this.$nextTick(() => {
		// 			uni.$wxTUICalling.init()
		// })
		// #endif
		uni.$TUIKit
			.setMessageRead({
				conversationID
			})
			.then(() => {
				logger.log('TUI-chat | setMessageRead  | ok');
			});
		uni.$TUIKit.getConversationProfile(conversationID).then(res => {
			const { conversation } = res.data;
			this.conversation = conversation;
			this.setData({
				conversationName: this.getConversationName(conversation),
				isShow: conversation.type === 'GROUP'
			});
		});
		this.getOrderData()
	},
	mounted() {},
	onUnload() {
		// #ifdef MP-WEIXIN
		//   this.$refs.TUICalling.destroyed();
		// // #endif
	},
	methods: {
		checkOrder(){
			console.log(11111)
			uni.navigateTo({
				url: '../index/indexOrderDetail?id=' + this.orderID + '&from=' + '1'
			})
		},
		getOrderData(){
			 var uid = this.conversationID.substr(9,this.conversationID.length-9)
			// console.log(uid)
			var that = this
			this.http.sendRequest('MerchantOrder.GetUserOrder',{'users_id':uid}).then(function(data) {
				console.log(data)
				if(data.code == 0){
					var dic = data.info[0]
					that.orderID = dic.id
					that.orderNumber = dic.order_id
				}
			})

		},
		stopVideoHander() {
			this.videoPlay = false;
		},
		getConversationName(conversation) {
			if (conversation.type === '@TIM#SYSTEM') {
				this.setData({
					showChat: false
				});
				return '系统通知';
			}

			if (conversation.type === 'C2C') {
				return conversation.remark || conversation.userProfile.nick || conversation.userProfile.userID;
			}

			if (conversation.type === 'GROUP') {
				return conversation.groupProfile.name || conversation.groupProfile.groupID;
			}
		},

		sendMessage(event) {
			// 将自己发送的消息写进消息列表里面
			this.$refs.messageList.updateMessageList(event.detail.message);
			this.sendnotifacation(event.detail.message.payload.text)
			// }
		},
		sendnotifacation(text) {
			var that = this
			that.http.sendRequest('Push.Send', {
				'to':that.conversationID.replace('C2C', ''),
				'content':text
			}).then(function(info) {
				
			})
		},

		triggerClose() {
			if (this.showChat) {
				this.$refs.messageInput.handleClose();
			}
		},

		goBack() {
			const pages = getCurrentPages(); // 当前页面栈

			if (
				pages[pages.length - 2].route === 'pages/TUI-Conversation/create-conversation/create' ||
				pages[pages.length - 2].route === 'pages/TUI-Group/create-group/create' ||
				pages[pages.length - 2].route === 'pages/TUI-Group/join-group/join'
			) {
				uni.navigateBack({
					delta: 2
				});
			} else {
				uni.navigateBack({
					delta: 1
				});
			}

			uni.$TUIKit
				.setMessageRead({
					conversationID: this.conversationID
				})
				.then(() => {});
		}
	}
};
</script>
<style>
@import './chat.css';
</style>
